import { Button, HStack, Flex, VStack, Tag, useBreakpointValue } from '@chakra-ui/react'
import React from 'react'
import { WordPin } from './WordPin'
import { useGameStore } from '../../app/store'
import { words5 } from '../../utils/words5'
import { words6 } from '../../utils/words6'

const words = {
    5: words5,
    6: words6
}

export const Helper = () => {
    const wordLength = useGameStore((state) => state.length)
    const [filter1, setFilter1] = React.useState(Array(wordLength).fill('.').join(""))
    const [page, setPage] = React.useState(0)

    const wordsOnPage = useBreakpointValue({base: 30, sm: 40, md: 50})

    const filteredWords = words[wordLength].filter(s => s.match(filter1))
    const lastPage = Math.round(filteredWords.length/wordsOnPage)

    filteredWords.sort()

    React.useEffect(() => {
        setPage(0)
    }, [wordLength, wordsOnPage])
    
    function nextPage(){
        setPage(page+1)
    }
    function prevPage(){
        setPage(page-1)
    }
    function resetPage(){
        setPage(0)
    }
    function setLastPage(){
        setPage(lastPage-1)
    }
    function handleComplete(value){
        setFilter1(value.map(l => l ? l : '.').join(""))
        setPage(0)
    }
    
    var wordsToDisplay = []
    for(let i = page*wordsOnPage; i < (page+1)*wordsOnPage && i < filteredWords.length; i++){
        wordsToDisplay.push(filteredWords[i])
    }
    return (
        <VStack>
            <WordPin handleComplete={handleComplete}/>
            <Flex
                width="100%"
                maxW="500px"
                overflow="auto" 
                justifyContent="center" 
                alignItems="center"
                shrink={1}
                grow={1}
                flexWrap="wrap"
            >
                {wordsToDisplay.map((w, i) => (
                    <Tag
                        key={i}
                        maxW="fit-content" 
                        display="inline-flex"
                        m={1}
                        shrink={1}
                        justifyContent="center"
                    >
                        {w}
                    </Tag>
                ))}
            </Flex>
            <HStack mt={3}>
                <Button disabled={page <= 0} onClick={prevPage}>{"<"}</Button>
                <Button onClick={resetPage}>{page+1}</Button>
                <Button disabled={page >= lastPage || wordsToDisplay.length < wordsOnPage } onClick={nextPage}>{">"}</Button>                
            </HStack>
            { lastPage > 5 ? (<HStack mt={2}>
                {page !== 0 ? <Button onClick={resetPage}>first</Button> : null}
                {page < lastPage-1 ? <Button onClick={setLastPage}>last</Button> : null}
            </HStack>) : null}
        </VStack>
    )
}