import { Button, Flex, HStack, Input, useColorModeValue } from '@chakra-ui/react'
import React from 'react'
import { useGameStore } from '../../app/store'

export const WordPin = ({ handleComplete }) => {
    const wordLength = useGameStore((state) => state.length)
    const [input, setInput] = React.useState(Array(wordLength).fill(""))

    const handleChange = ({ target }) => {
        const copy = input.slice()
        const index = parseInt(target.id)
        copy[index] = target.value[target.value.length-1]?.trim().toLowerCase()
        if(!isNaN(copy[index])){
            copy[index] = ""
        }
        setInput(copy)
    }
    function clear(){
        const clearArr = Array(wordLength).fill("")
        setInput(clearArr)
        handleComplete(clearArr)
    }
    React.useEffect(() => {
        setInput(Array(wordLength).fill(""))
    }, [wordLength])

    const inputBg = useColorModeValue("white", "black")
    return (
        <Flex flexWrap="wrap" justify="center" align="center">
            <HStack
                m="1em 0"
                maxW="650px"
            >
                {input.map((l, i) => ((
                <Input
                    textAlign="center"
                    value={l}
                    id={i}
                    key={i}
                    maxLength={2}
                    onChange={handleChange}
                    className="chakra-pin-input"
                    w="3em"
                    h="3em"
                    background={inputBg}
                    border="3px solid"
                />)))}
            </HStack>
            <Button size="md" onClick={() => handleComplete(input)} ml={2}>
                Filter
            </Button>
            { input.join("") ? 
            <Button onClick={clear} size="md" ml={1}>
                Clear
            </Button> : null }
        </Flex>
    )
}